<template>
    <div class="exchange_container">
        <head-top head-title="兑换红包" goBack="true"></head-top>
        <section class="exchange_se">
            <div class="exchange_input">
                <input type="text" v-model="duihuanma" placeholder="输入兑换码">
            </div>
            <div class="exchange_inputs" @click="initdata()">
                 <input type="text" v-model="getimgnum" placeholder="输入验证码" maxlength="4">
                <div class="img_tx">
                    <img :src="getCaptchasimg" alt="">
                    <div class="tx_s">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </div>
            <div class="bottom_type" :class="{'buttom_color':state}" @click="submitduihuan">兑换</div>
        </section>
        <alert-tip v-show="showAlert" :alertText="alertText" @closeTip="showAlert=false"></alert-tip>
    </div>
</template>

<script>
import headTop from '../../../components/header/header'
import alertTip from '../../../components/common/alertTip'
import {mapState,mapMutations} from 'vuex'
import {getcaptchas, exChangeHongbao} from '../../../service/getData'
export default {
    components: {
        headTop,
        alertTip
    },computed: {
        ...mapState([
            'userInfo'
        ]),
        state:function()
        {
            let states=this.duihuanma!=''&&this.getimgnum!=''&&this.getimgnum.length==4;
            return states;
        }
    },methods: {
       async initdata()
        {
             let res=await getcaptchas();
             this.getCaptchasimg=res.code;
        },
        //提交兑换码
        async submitduihuan()
        {
            if(this.userInfo&&this.userInfo.user_id)
            {
                if(this.state)
                {
                    let res=await exChangeHongbao(this.userInfo.user_id,this.duihuanma,this.getimgnum);
                    if(res.message)
                    {
                        this.showAlert=true;
                        this.alertText=res.message;
                    }
                    this.initdata();
                }
            }
        }
    },data () {
        return {
            showAlert:false,
            alertText:null,
            duihuanma:'',
            getCaptchasimg:null,
            getimgnum:''
        }
    },mounted () {
        this.initdata();
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.exchange_container{
    position: fixed;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    background: #f1f1f1;
    z-index: 240;
    padding-top: 1.95rem;
}
.exchange_se{
   padding:0.5rem;
    .exchange_input{
        margin-top: 0.4rem;
        display: flex;
        input{
            @include wh(100%,1.9rem);
            padding:0.2rem;
            @include sc(0.6rem,#333);
            border:1px solid #f1f1f1;
            border-radius: 0.2rem;
        }
    }
    .exchange_inputs{
        display: flex;
        margin-top: 0.3rem;
        input{
              @include wh(100%,1.9rem);
            padding:0.2rem;
            @include sc(0.6rem,#333);
            border:1px solid #f1f1f1;
            border-radius: 0.2rem;
            flex:1.8;
        }
        .img_tx{
            margin-left:0.4rem;
            background: #fff;
            display: flex;
            flex:1.5; 
            .tx_s{
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                align-items: center;
                padding-left:0.9rem;
                padding-top: 0.3rem;
                p{
                    @include sc(0.5rem,#333);
                }
                p:nth-of-type(2)
                {
                    color:$blue;
                }
            }
            img{
                align-items: center;
                padding-left:0.1rem;
                padding-top: 0.3rem;
                @include wh(3.5rem,1.5rem);
            }
        }
    }
}
.bottom_type{
    margin-top: 0.5rem;
    padding:0.5rem;
    background: #ccc;
    text-align: center;
    color:#fff;
    font-size: 0.6rem;
    border-radius: 0.2rem;
}
.buttom_color{
    background: #4cd964;
}
</style>



